<template>
  <Auth>
    <b-overlay :show="showLoader" rounded="sm">
      <div class="row justify-content-center">
        <div class="col-md-8 col-lg-6 col-xl-5">
          <div class="card">
            <div class="card-body p-4">
              <div class="text-center w-75 m-auto">
                <div class="auth-logo">
                  <router-link to="/" class="logo logo-dark text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo.svg" alt height="22" />
                  </span>
                  </router-link>

                  <router-link to="/" class="logo logo-light text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo-light.png" alt height="22" />
                  </span>
                  </router-link>
                </div>
                <p class="text-muted mb-4 mt-3">
                  A verification code has been sent to your email. Please enter the code
                </p>
              </div>

              <form @submit.prevent="VerifyCustomerOtp" >
                <AlertMessage :variant="dangerSuccess" v-if="responseMessage" :message="responseMessage" />
                <div class="form-group mb-3" data-password="true">
                  <label for="verificationcode"> Enter verification code</label>
                  <div class="input-group input-group-merge">
                  <input
                      class="form-control"
                      :type="[showPassword ? 'text' : 'password']"
                      id="verificationcode"
                      placeholder="Enter verification code"
                      v-model="verificationcode"
                      v-validate="'required'"
                      :class="{'is-invalid': vErrors.has('verificationcode') }"
                      name="verificationcode"
                  />
                  <div class="input-group-append" data-password="false">
                    <div class="input-group-append" data-password="true">

                            <span class="input-group-text"  @click="showPassword = !showPassword">
                               <i class="fa" :class="[showPassword ? 'fa-eye' : 'fa-eye-slash']" aria-hidden="true"></i>
                            </span>
                    </div>
                  </div>
                  </div>
                  <span v-show="vErrors.has('verificationcode')" class="help text-danger">{{ vErrors.first('verificationcode') }}</span>
                </div>
                <div class="form-group mb-0 text-center">
                  <button class="btn btn-primary btn-block" id="bkcolor" type="submit">
                    Verify
                  </button>
                </div>
              </form>

            </div>
            <!-- end card-body -->
          </div>
          <!-- end card -->

          <div class="row mt-3">
            <div class="col-12 text-center">
              <p>

              </p>
              <p class="text-muted">
                Didn't receive the code?
                <router-link
                    to="/login"
                    class="text-primary font-weight-medium ml-1"
                >Resend</router-link
                >
              </p>
            </div>
            <!-- end col -->
          </div>
          <!-- end row -->
        </div>
        <!-- end col -->
      </div>
    </b-overlay>
  </Auth>
</template>

<script>
import Auth from  '../../components/register/Auth'
import constant from '../../components/constant/index';
import customerInfo from '../../protos/Customer_grpc_web_pb';
import AlertMessage from "../../components/shared/AlertMessage";

export default {
  data(){
    return{
      showPassword : false,
      showLoader: false,
      dangerSuccess:'',
      responseMessage:'',
      verificationcode:'',
      emailaddress:'',
      clients:{}
    }
  },
  components:{
    Auth,
    AlertMessage
  },
  methods:{
    VerifyCustomerOtp(){
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.showLoader = true;
          this.responseMessage = '';
          this.emailaddress = this.$store.getters.getUserEmailAddress.emailAddress;
          console.log("Email Address : " , this.emailaddress);
          let request = new customerInfo.VerifyOTPRequestMessage();

          request.setOtpvalue(this.verificationcode);
          request.setCustomerid(this.$store.getters.getUserEmailAddress.emailAddress);
          request.setPersonalbusinessvalue("2");
          request.setLanguageid("en")
          console.log("Data : " , request)
          this.clients.verifyTransOTPNow(request, {}, (err,res) =>{
            this.showLoader = false;
            if(res.getIssuccess()){
               this.$store.dispatch('PasswordResetOtpvalue', {
                 otpvalue : this.verificationcode
               })
              this.$router.push('/business/forgotpassword').catch(() => { })
            }
            else {
              this.dangerSuccess = 'danger';
              this.responseMessage = res.getMessage();
            }
          });
          return;
        }
      });
    }
  },
  mounted() {
    this.clients = new customerInfo.CustomerAccountClient(constant.URL,null,null)
  }
}
</script>


<style scoped>

</style>